<template>
   <view >
     <uni-forms-item  label-width="0px">
        <uni-file-picker 
        limit="1" 
        title="上传头像"  
        :imageStyles="imageStyles"
        file-mediatype="image"
        file-extname="png,jpg"
        @select="select" 
        @progress="progress" 
        @success="success" 
        @fail="fail"
        :auto-upload="false"
         ref="files"></uni-file-picker>
     </uni-forms-item>
  </view>
</template>

<script>
  export default {
    name:"upload",
    data() {
      return {
        imageStyles: {
            width: 100,
            height: 100,
          },
      };
    },
    methods:{
      select(e){
        console.log('选择文件：',e)
      },
      // 获取上传进度
      progress(e){
        console.log('上传进度：',e)
      },
      
      // 上传成功
      success(e){
        console.log('上传成功')
      },
      
      // 上传失败
      fail(e){
        console.log('上传失败：',e)
      }
    }
  }
</script>

